import api from '../../api/api.js';
export default {
	data() {
		return {
			scrollLeft: 0,
			fixedTop: 0,
			showLoadmore: false,
			showTopLoading: false,
			tabCur: 0,
			page: 1,
			isLoading: true,
			dataList: [],
			CustomBar: this.CustomBar,
			tabs: [{
					name: "全部",
					category: "all"
				},
				{
					name: "Android",
					category: "Android"
				},
				{
					name: "iOS",
					category: "iOS"
				},
				{
					name: "App",
					category: "App"
				},
				{
					name: "前端",
					category: "前端"
				},
				{
					name: "瞎推荐",
					category: "瞎推荐"
				},
				{
					name: "拓展资源",
					category: "拓展资源"
				},
				{
					name: "休息视频",
					category: "休息视频"
				}
			]
		}
	},
	//组件生命周期方法
	created: function() {



	},
	mounted: function() {
		this.getListData();
	},
	methods: {
		// 页面生命周期方法
		onReachBottom() {
			console.log("reachBottom")
		},
		//加载更多
		scrollToLoadMore() {
			if (!this.showTopLoading && this.isLoading) {
				this.getListData();
			}
		},
		onTabSelected(event) {
			this.tabCur = event.currentTarget.dataset.index;
			this.scrollLeft = (event.currentTarget.dataset.index - 1) * 60;
			this.page = 1;
			this.isLoading = true;
			this.getListData();
		},
		// 获取数据列表
		getListData() {
			if (this.page == 1) {
				this.showTopLoading = true;
			} else {
				this.showLoadmore = true;
			}
			api.get(`/data/${this.tabs[this.tabCur].category}/10/${this.page}`)
				.then(res => {
					this.dataList = this.page == 1 ? res.results : this.dataList.concat(res.results);
					this.showTopLoading = false;
					//判断是否显示加载更多
					if (res.results.length < 10) {
						this.showLoadmore = false;
					} else {
						this.showTopLoading = false;
						this.page++;
					}
				}).catch(e => {
					if (this.page == 1) {
						this.dataList = [];
						this.showTopLoading = true;
						this.showLoadmore = false;
					} else {
						this.showTopLoading = false;
						this.showLoadmore = true;
					}
					this.isLoading = false;
				})
		},
		//item点击事件
		onItemClick(e) {
			let url = e.currentTarget.dataset.url;
			uni.navigateTo({
				url: `/pages/web/web?url=${encodeURIComponent(url)}`
			})
		}
	}
}
